import React, { useEffect, useState } from "react";
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Collapse from '@material-ui/core/Collapse';
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import { ArrowDropDownCircle, VerticalAlignTop } from "@material-ui/icons";
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import TextField from '@material-ui/core/TextField'; 
import match3 from './test.gif'
import {
  fade,
  ThemeProvider,
  withStyles,
  createMuiTheme,
} from '@material-ui/core/styles';
import { Stage, Layer, Rect, Text, Circle, Line } from 'react-konva';


 

const useRowStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      borderBottom: 'unset',
    },
  },
  nested3: {
    paddingLeft: theme.spacing(8),
  },
  nested2: {
    paddingLeft: theme.spacing(8),
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));

function createData(name, pre, post) {
  return {
    name,
    pre,
    post,
  };
}

function DrawArea(props) {
  const [tool, setTool] = React.useState('pen');
  const [lines, setLines] = React.useState([]);
  const isDrawing = React.useRef(false);
  
  const handleMouseDown = (e) => {
    isDrawing.current = true;
    const pos = e.target.getStage().getPointerPosition();
    setLines([...lines, { tool, points: [pos.x, pos.y] }]);
  };

  const handleMouseMove = (e) => {
    // no drawing - skipping
    if (!isDrawing.current) {
      return;
    }
    const stage = e.target.getStage();
    const point = stage.getPointerPosition();
    let lastLine = lines[lines.length - 1];
    // add point
    lastLine.points = lastLine.points.concat([point.x, point.y]);

    // replace last
    lines.splice(lines.length - 1, 1, lastLine);
    setLines(lines.concat());
  };

  const handleMouseUp = () => {
    isDrawing.current = false;
  };

  return (
    <Paper variant="outlined" square color="#dddddd">
      <Stage
        width={300}
        height={200}
        onMouseDown={handleMouseDown}
        onMousemove={handleMouseMove}
        onMouseup={handleMouseUp}
      >
        <Layer>
          {lines.map((line, i) => (
            <Line
              key={i}
              points={line.points}
              stroke="#df4b26"
              strokeWidth={5}
              tension={0.5}
              lineCap="round"
              globalCompositeOperation={
                line.tool === 'eraser' ? 'destination-out' : 'source-over'
              }
            />
          ))}
        </Layer>
      </Stage>
      <select
        value={tool}
        onBlur={(e) => {
          setTool(e.target.value);
        }}
      >
        <option value="pen">Pen</option>
        <option value="eraser">Eraser</option>
      </select>
    </Paper>
  );
}

function Row(props) {
  const { row, setRow, rows } = props;
  const classes = useRowStyles();

  const handleInputChange = e => {
    const {name, value} = e.target
    setRow( prevRows => (prevRows.map((r1) => r1.id === row.id? {...r1, [name]: value}: r1 )
    )
  )  
}

  return (
    <React.Fragment>
      <TableRow className={classes.root}>
        <TableCell>
          <IconButton aria-label="delete row" size="small" onClick={() => row.onDelete(row)}>
          <DeleteIcon />
          </IconButton>
        </TableCell>
        <TableCell component="th" scope="row">
          <ThemeProvider >
            <TextField
              className={classes.margin}
              label="Name"
              variant="outlined"
              id="mui-theme-provider-outlined-input"
              placeholder="What is the Mechanic's name?"
              fullWidth
              multiline
              name='name'
              defaultValue={row.name}
              onBlur={handleInputChange}
            ></TextField>
          </ThemeProvider>
        </TableCell>
        <TableCell scope="row"><ThemeProvider >
          <TextField
            className={classes.margin}
            label="IF"
            variant="outlined"
            id="mui-theme-provider-outlined-input"
            placeholder="What triggers the mechanic?"
            fullWidth
            multiline
            name='pre'
            defaultValue={row.pre}
            onBlur={handleInputChange}
          ></TextField>
        </ThemeProvider></TableCell>
        <TableCell ><ThemeProvider >
          <TextField
            className={classes.margin}
            label="THEN"
            variant="outlined"
            id="mui-theme-provider-outlined-input"
            placeholder="What is the effect of the mechanic triggering?"
            fullWidth
            multiline
            name='post'
            defaultValue={row.post}
            onBlur={handleInputChange}
          ></TextField>
        </ThemeProvider></TableCell>
      </TableRow>
      <TableRow>
        <TableCell></TableCell>
        <TableCell></TableCell>
        <TableCell width="30%"><DrawArea/></TableCell>
        <TableCell width="30%"><DrawArea/></TableCell>
      </TableRow>
    </React.Fragment>
  );
}

Row.propTypes = {
  row: PropTypes.shape({
    id: PropTypes.number.isRequired,
    name: PropTypes.string.isRequired,
    pre: PropTypes.string.isRequired,
    post: PropTypes.string.isRequired,
    onDelete: PropTypes.func.isRequired,
  }).isRequired,
};



export default function MechanicsTable(props) {
  const removeRow = (id) => {
    var tempRows = [...rows];
    const index = tempRows.indexOf(id);
    if (index !== -1) {
      tempRows.splice(index, 1);
    }
    setRow(rows= [...tempRows])
  };
  
  const [open, setOpen] = React.useState(false);
  const [open2, setOpen2] = React.useState(false);
  const [open3, setOpen3] = React.useState(false);
  const classes = useRowStyles();

  const updateRow = () => {
    setRow()
  };

  const [rowID, setRowID] = useState(1)
  const { rows } = props;
  const { setRow } = props;

  const addRow = () => {
    setRowID(rowID => rowID + 1)
    setRow(rows => [
      ...rows,
      {
        id: rowID,
        name: "",
        pre: '',
        post: '',
        gameplayExperiences: [],
        gameplayExperienceDescription: "",
        onDelete: removeRow
      }
    ])
  };

  const addRowElement = (n, pr, po) => {
    setRowID(rowID => rowID + 1)
    setRow(rows => [
      ...rows,
      {
        id: rowID,
        name: n,
        pre: pr,
        post: po,
        gameplayExperiences: [],
        gameplayExperienceDescription: "",
        onDelete: removeRow
      }
    ])
  };

  return (
    
    <TableContainer component={Paper}>
      <Table aria-label="collapsible table">
        <TableHead>
          <TableRow>
            <TableCell width="5%"></TableCell>
            <TableCell width="30%">Mechanic</TableCell>
            <TableCell width="30%" align="right">If</TableCell>
            <TableCell width="30%" align="right">Then</TableCell>
          </TableRow>
        </TableHead>
        <TableRow>
          <TableCell>
          </TableCell>
          <TableCell style={{VerticalAlignTop}}><IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
            {open ? <Typography>Hide Mechanic Browser</Typography> : <Typography>Show Mechanic Browser</Typography>}
          </IconButton></TableCell>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={4}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              <ListItem button className={classes.nested}>
                <ListItemText primary="Tile Changes" onClick={() => setOpen2(!open2)} />
              </ListItem>
              <Collapse in={open2} timeout="auto" unmountOnExit>
                <List component="div" disablePadding>
                  <ListItem button className={classes.nested2}>
                    <ListItemText primary="Remove Tiles"  onClick={() => setOpen3(!open3)}/>
                  </ListItem>
                  <Collapse in={open3} timeout="auto" className={classes.nested3} unmountOnExit>
                  <Table aria-label="collapsible table" className={classes.nested3}>
                    <TableHead>
                      <TableRow>
                        <TableCell width="30%"><ThemeProvider >
                          <TextField
                            className={classes.margin}
                            label="Name"
                            variant="outlined"
                            id="mui-theme-provider-outlined-input"
                            fullWidth
                            multiline
                            InputProps={{
                              readOnly: true,
                            }}
                            defaultValue="Remove Tiles"
                          ></TextField>
                        </ThemeProvider></TableCell>
                        <TableCell width="30%"><ThemeProvider >
                          <TextField
                            className={classes.margin}
                            label="Name"
                            variant="outlined"
                            id="mui-theme-provider-outlined-input"
                            fullWidth
                            multiline
                            InputProps={{
                              readOnly: true,
                            }}
                            defaultValue="If three boxes are in a row or column"
                          ></TextField>
                        </ThemeProvider></TableCell>
                        <TableCell width="30%"><ThemeProvider >
                          <TextField
                            className={classes.margin}
                            label="then"
                            variant="outlined"
                            id="mui-theme-provider-outlined-input"
                            fullWidth
                            multiline
                            InputProps={{
                              readOnly: true,
                            }}
                            defaultValue="Then the boxes are removed from the game"
                          ></TextField>
                        </ThemeProvider></TableCell>

                      </TableRow>
                      <TableRow className={classes.nested3}>
                        <TableCell>
                        <Button variant="contained" color="primary" onClick={() => {addRowElement("Remove Tiles", "If three boxes are in a row or column", "Then the boxes are removed from the game")}}>Select Mechanic</Button>
                        </TableCell>
                        <TableCell>
                            <img src={match3} width="100%"/>
                        </TableCell>
                        <TableCell>

                        </TableCell>
                      </TableRow>
                    </TableHead>
                    </Table>
                    </Collapse>
                    <ListItem button className={classes.nested2}>
                      <ListItemText primary="Modify Tile" />
                    </ListItem>
                    <ListItem button className={classes.nested2}>
                      <ListItemText primary="Combine Tiles" />
                    </ListItem>
                    <ListItem button className={classes.nested2}>
                      <ListItemText primary="Create Tile" />
                    </ListItem>
                  
                </List>
              </Collapse>
              <ListItem button className={classes.nested}>
                <ListItemText primary="Player Interaction" />
              </ListItem>
              <ListItem button className={classes.nested}>
                <ListItemText primary="Automation" />
              </ListItem>
            </List>
          </Collapse>
        </TableCell>
      </TableRow>
        <TableBody>
          {console.log(rows),
          rows.map((row) => (
            row.onDelete = removeRow,
            <Row key={row.id} row={row} setRow={setRow} rows={rows}/>
          ))}
        <TableRow>
          <TableCell/>
          <TableCell>
              <Button variant="contained" color="primary" onClick={addRow}>Add Mechanic</Button>
            </TableCell>
        </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
}