import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import DraftsIcon from '@material-ui/icons/Drafts';
import SendIcon from '@material-ui/icons/Send';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';

const useStyles = makeStyles((theme) => ({
    root: {
      width: '100%',
      maxWidth: 360,
      backgroundColor: theme.palette.background.paper,
    },
    nested: {
      paddingLeft: theme.spacing(4),
    },
    nested2: {
      paddingLeft: theme.spacing(8),
    },
  }));

export default function NestedList() {
    const classes = useStyles();
    const [openPE, setOpenPE] = React.useState(false);
    const [openME, setOpenME] = React.useState(false);

    const handleClickPE = () => {
      setOpenPE(!openPE);
    };

    const handleClickME = () => {
        setOpenME(!openME);
    };
  
    return (
      <List
        component="nav"
        aria-labelledby="nested-list-subheader"
        className={classes.root}
      >

<ListItem button onClick={handleClickME}> 
          <ListItemText primary="Executive Summary" />
          {openME ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={openPE} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItem button className={classes.nested}>
              <ListItemText primary="Name and Premise" />
            </ListItem>
            <ListItem button className={classes.nested}>
              <ListItemText primary="Theme" />
            </ListItem>
            <ListItem button className={classes.nested}>
              <ListItemText primary="Key Points" />
            </ListItem>
          </List>
        </Collapse>

        <ListItem button onClick={handleClickME}> 
          <ListItemText primary="Game Description" />
          {openME ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={openPE} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItem button className={classes.nested}>
              <ListItemText primary="Background and Characters" />
            </ListItem>
            <ListItem button className={classes.nested}>
              <ListItemText primary="Levels" />
            </ListItem>
            <ListItem button className={classes.nested}>
              <ListItemText primary="Game Mechanics" />
            </ListItem>
          </List>
        </Collapse>

        <ListItem button onClick={handleClickME}> 
          <ListItemText primary="Design Goals" />
          {openME ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={openME} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            
        <ListItem button onClick={handleClickPE} className={classes.nested}>
          <ListItemText primary="Player Experience" />
          {openPE ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={openPE} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItem button className={classes.nested2}>
              <ListItemText primary="Key Moment Pacing" />
            </ListItem>
            <ListItem button className={classes.nested2}>
              <ListItemText primary="Player Goal" />
            </ListItem>
            <ListItem button className={classes.nested2}>
              <ListItemText primary="Goal Challenge" />
            </ListItem>
            <ListItem button className={classes.nested2}>
              <ListItemText primary="Player Reward" />
            </ListItem>
            <ListItem button className={classes.nested2}>
              <ListItemText primary="Player Failure" />
            </ListItem>
            <ListItem button className={classes.nested2}>
              <ListItemText primary="Failure Consequences" />
            </ListItem>
            <ListItem button className={classes.nested2}>
              <ListItemText primary="Experience Focus" />
            </ListItem>
            <ListItem button className={classes.nested2}>
              <ListItemText primary="Avatar Control" />
            </ListItem>
          </List>
        </Collapse>
        <ListItem button onClick={handleClickPE} className={classes.nested}>
          <ListItemText primary="Gameplay Experience" />
          {openPE ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        </List>
        </Collapse>

      </List>
    );
  }