function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}


TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

const drawerWidth = 0;


function CharacterSheetRow(props) {
    const { row } = props;
    const { rows } = props;
    const { setFunction } = props;
    const [open, setOpen] = React.useState(false);
    const classes = useStyles();

    return (
        <React.Fragment>
            <TableRow>
                <TableCell>
                    <IconButton aria-label="delete row" size="small" onClick={() => row.onDelete(row.id, rows, setFunction)}>
                        {open ? <DeleteIcon /> : <DeleteIcon />}
                    </IconButton>
                </TableCell>
                <TableCell component="th" scope="row">
                    <ThemeProvider >
                        <TextField
                            className={classes.margin}
                            label="Character"
                            variant="outlined"
                            id="mui-theme-provider-outlined-input"
                            placeholder="What is the character's name?"
                            fullWidth
                            multiline
                            defaultValue={row.name}
                        ></TextField>
                    </ThemeProvider>
                </TableCell>
                <TableCell scope="row"><ThemeProvider >
                    <TextField
                        className={classes.margin}
                        label="Description"
                        variant="outlined"
                        id="mui-theme-provider-outlined-input"
                        placeholder="This character is ..."
                        fullWidth
                        multiline
                        defaultValue={row.description}
                    ></TextField>
                </ThemeProvider></TableCell>
            </TableRow>
        </React.Fragment>
    );
}

CharacterSheetRow.propTypes = {
    row: PropTypes.shape({
        id: PropTypes.number.isRequired,
        name: PropTypes.string.isRequired,
        description: PropTypes.string.isRequired,
        onDelete: PropTypes.func.isRequired,
    }).isRequired,
};

