function LevelsRow(props) {
    const { row, setRow } = props;
    const [open, setOpen] = React.useState(false);
    const classes = useStyles();
    
    const handleInputChange = e => {
        const {name, value} = e.target
        setRow( prevRows => (prevRows.map((r1) => r1.id === row.id? {...r1, [name]: value}: r1 )
        )
      )  
    }


    return (
        <React.Fragment>
            <TableRow>
                <TableCell>
                    <IconButton aria-label="delete row" size="small" onClick={() => row.onDelete(row)}>
                        {open ? <DeleteIcon /> : <DeleteIcon />}
                    </IconButton>
                </TableCell>
                <TableCell component="th" scope="row">
                    <ThemeProvider >
                        <TextField
                            className={classes.margin}
                            label="Level Name"
                            variant="outlined"
                            id="mui-theme-provider-outlined-input"
                            placeholder="What is the level's name?"
                            fullWidth
                            multiline
                            name='name'
                            defaultValue={row.name}
                            onBlur={handleInputChange}
                        ></TextField>
                    </ThemeProvider>
                </TableCell>
                <TableCell scope="row"><ThemeProvider >
                    <TextField
                        className={classes.margin}
                        label="Description"
                        variant="outlined"
                        id="mui-theme-provider-outlined-input"
                        placeholder="What is the level's description?"
                        fullWidth
                        multiline
                        name='description'
                        defaultValue={row.description}
                        onBlur={handleInputChange}
                    ></TextField>
                </ThemeProvider></TableCell>
            </TableRow>
        </React.Fragment>
    );
}

